}

// select 2 customization
.select2-container {
  width: 100% !important;
}

.select2-container--default .select2-selection--single .select2-selection__arrow b:before {
  position: absolute;
  content: "\f110";
  right: 1rem;
  font-weight: 400;
  top: 50%;
  transform: translateY(-50%);
  font-family: "LineAwesome";
