}

/* custom photo upload */
.custom-photo-upload {
  position: relative;

  input {
    width: remy(200px);
    height: remy(200px);
    position: absolute;
    left: 0;
    top: 0;
    opacity: 0;
    margin: 0;
    cursor: pointer;
  }

  .custom-upload-box {
    .image-box {
      width: remy(200px);
      height: remy(200px);
      border: 1px solid #e4e8ed;
      border-radius: 3px;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;

      .icon {
        font-size: remy(100px);
        color: $gray-100;
      }

      span {
        color: $light-gray;

        &.file-name {
          text-align: center;
        }
      }
    }

    .image-info {
      display: flex;
      justify-content: center;
      flex-direction: column;
      margin-left: remy(30px);

      span {
        color: $light-gray;
      }
    }
  }
}

/* custom upload */
.custom-upload {
  position: relative;

  input {
    width: remy(200px);
    height: remy(50px);
    position: absolute;
    left: 0;
    top: 0;
    opacity: 0;
    margin: 0;
    cursor: pointer;
  }

  .custom-upload-box {
    .upload-info {
      display: flex;
      justify-content: center;
      flex-direction: column;
      margin-left: remy(30px);

      span {
        color: $light-gray;
      }
    }
  }
}

